import React from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import { Container, Card } from 'Common'
import { Wrapper, Grid, Item, Content, Stats } from './styles'

export const Blogs = () => {
	const {
		allMediumPost: { edges },
	} = useStaticQuery(graphql`
		{
			allMediumPost(sort: { fields: [createdAt], order: DESC }) {
				edges {
					node {
						id
						title
						virtuals {
							subtitle
							previewImage {
								imageId
							}
						}
						author {
							name
						}
					}
				}
			}
		}
	`)
	return (
		<Wrapper as={Container} id="blogs">
			<h2>Blogs</h2>
			<Grid>
				{edges.map(({ node }) => (
					<Item
						key={node.id}
						as="a"
						href="https://medium.com/@rajeshdavid"
						target="_blank"
						rel="noopener noreferrer"
					>
						<Card>
							<Content>
								<h4>{node.title}</h4>
								<img
									src={`https://cdn-images-1.medium.com/max/400/${
										node.virtuals.previewImage.imageId
									}`}
								/>
								<p>{node.virtuals.subtitle}</p>
							</Content>
						</Card>
					</Item>
				))}
			</Grid>
		</Wrapper>
	)
}
